<style>
#sheet-pannel {
  background-color: #fff;
  padding: 16px 32px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.35)
}

#sheet-zone {
  /*margin:16px 0*/
}
.block {
  display: block;
}
select {
  background: transparent;
  border: 0;
  border-bottom: 1px solid #DADADA;
  width: 200px;
  padding: 5px;
  font-size: 16px;
  line-height: 1;
  border-radius: 0;
  height: 34px;
  -webkit-appearance: none;
}

/* 必需 */
#sheet-pannel .expand-transition {
  transition: all .3s ease;
  height: 200px;
  padding: 10px 0;
  overflow: hidden;
}

/* .expand-enter 定义进入的开始状态 */
/* .expand-leave 定义离开的结束状态 */
#sheet-pannel .expand-enter, #sheet-pannel .expand-leave {
  height: 0;
  padding: 0 10px;
  opacity: 0;
}

.qcollection {
  background-color: #fff;
  padding: 16px;
  border-radius: 3px;
  box-shadow: 0 1px 4px 0 rgba(0,0,0,0.14);
  height: 100%
}

.qc-subject {
  background-color: #2196F3;
color: #fff;
padding: 4px 8px;
border-radius: 14px;
display: inline-block;
margin-top: 3px;
}
.qc-title {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
  padding-left: 12px;
  border-left: 4px solid #3F51B5;
  margin-top: 40px

}
.qc-public {
  color: #E91E63;
  float: right;
  padding: 8px;
}
</style>
<template>
  <div id="home">
    <div id="sheet-pannel">
      <mdl-button primary raised @click="showCreatSheet = true">
        創建新題集
      </mdl-button>
      <div id="sheet-zone" v-show="showCreatSheet" transition="expand">
        <div class="block">
          <mdl-textfield floating-label="題集名字" :value.sync="newQcollection.name"></mdl-textfield>
        </div>
        <div class="block">
          科目：
          <select v-model="newQcollection.subject">
            <option v-for="subject in subjects" v-bind:value="subject.id">
              {{ subject.name }}
            </option>
          </select>
        </div>
        <div class="block" style="padding:20px 0">
          <mdl-checkbox :checked.sync="newQcollection.public">公開</mdl-checkbox>
        </div>
        <mdl-button accent raised v-on:click="createNewQcollection">
          創建
        </mdl-button>
        <mdl-button v-show="showCreatSheet" @click="showCreatSheet = false">
          關閉
        </mdl-button>
      </div>
    </div>
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--2-col qcollection" v-for="qc in myQcollections">
        <span class="qc-subject">{{idToName(qc.subject)}}</span>
        <span class="qc-public">{{booleanToPublic(qc.public)}}</span>
        <p class="qc-title">{{qc.name}}
      </div>
    </div>
  </div>
</template>

<script>
import vmdl from 'vue-mdl'
import Vue from 'vue'

vmdl.register(Vue, 'mdlCheckbox')
vmdl.register(Vue, 'mdlButton')
vmdl.register(Vue, 'mdlTextfield')

var checkbox = vmdl.components['mdlCheckbox']
var button = vmdl.components['mdlButton']
var textfield = vmdl.components['mdlTextfield']

export default {
  ready: function () {
    this.getMyQcollections()
  },
  components: {
    mdlButton: button,
    mdlCheckbox: checkbox,
    mdlTextfield: textfield
  },
  methods: {
    getMyQcollections: function () {
      this.$http.get('/api/manage-qcollection/mine').then(function (response) {
        this.myQcollections = response.data
        console.log(response)
      }, function (response) {
        console.log(response)
      })
    },
    createNewQcollection: function () {
      if (this.newQcollection.name) {
        this.$http.post('/api/manage-qcollection/new', this.newQcollection).then(function (response) {
          this.showCreatSheet = false
          this.getMyQcollections()
        }, function (response) {
          console.log(response)
        })
      }
    },
    idToName: function (id) {
      var array = this.subjects
      for (var i = 0; i < array.length; i++) {
        if (array[i].id === id) {
          return array[i].name
        }
      }
    },
    booleanToPublic: function (boolean) {
      if (boolean) {
        return '公開'
      } else {
        return '私有'
      }
    }
  },
  data () {
    return {
      myQcollections: [],
      showCreatSheet: false,
      newQcollection: {
        name: '',
        subject: 'math',
        public: true,
        questions: []
      },
      subjects: [
        { name: '數學', id: 'math' },
        { name: '物理', id: 'phy' }
      ]
    }
  }
}
</script>



